import React from 'react';
import axios from 'axios';


class History extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            historys:[]
        }
    }
    componentDidMount(){
        axios.get(`http://localhost:5000/booking/history`)
        .then((res)=>{
            this.setState({
                historys:res.data
            })
        })
    }
    render(){
        return(
            <div className= "container">
                <h2 className="center">History</h2>
                <div className="container-box">
                <div class="row">
                    <div class="col s6 m6 l4"></div>

                <table>
                <thead>
                <tr>
                    <th>Source</th>
                    <th>Destination</th>
                    <th>Fare</th>
                    <th>Date</th>
                </tr>
                </thead>

                <tbody>{
                        this.state.historys.map(val=>

                        <tr>
                            <td>{val.source}</td>
                            <td>{val.destination}</td>
                            <td>{val.fare}</td>
                            <td>{val.bookingdate}</td>
                        </tr>

                        )
                    }</tbody>
                </table>


            </div>
            </div>
            </div>

        )
    }
}

export default History
